.post{
    display: flex;
    flex-direction: column;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    background-color: white;
    max-width: 500px;
    border: 1px solid lightgray;
    margin-bottom: 45px;
}

.post_image{
    width: 100%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
}

.post_text{
    font-weight: normal;
    padding: 20px;
    border-bottom: 1px solid lightgray;
}

.post_comments{
    padding: 20px;
} 

.post_header{
    display: flex;
    align-items: center;
    padding: 25px;
}

.post_headerInfo{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: space-between;
    margin-left: 10px;
}

.post_button{
    flex: 0;
    border: none;
    border-top: 1px solid lightgray;
    background-color: transparent;
    color: lightblue;
}

.post_commentbox{
    display: flex;
    margin-top: 10px;

}

.post_input{
    flex: 1;
    border: none;
    border-top: 1px solid lightgray;
    padding: 10px;
}
